input {
  outline: none;
}
._right {
  float: right;
}
._relative {
  position: relative;
}
._absolute {
  position: absolute;
}
._fixed {
  position: fixed;
}
.ripple {
  background-color: rgba(255, 255, 255, 0.4);
  -webkit-animation: ripple 0.8s forwards cubic-bezier(0, 0, 0.2, 1);
          animation: ripple 0.8s forwards cubic-bezier(0, 0, 0.2, 1);
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #505460;
}
input:-moz-placeholder,
textarea:-moz-placeholder {
  color: #505460;
}
input::-moz-placeholder,
textarea::-moz-placeholder {
  color: #505460;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #505460;
}
@-webkit-keyframes ripple {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
}
80% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    opacity: 0;
}
}
@keyframes ripple {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
}
80% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    opacity: 0;
}
}
@-webkit-keyframes slideOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
}
@keyframes slideOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
}
@-webkit-keyframes slideIn {
from {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
@keyframes slideIn {
from {
    -webkit-transform: translate3d(100vw, 0, 0);
    transform: translate3d(100vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
@-webkit-keyframes thirdSildeOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
}
@keyframes thirdSildeOut {
from {
    -webkit-transform: none;
    transform: none;
}
to {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
}
@-webkit-keyframes thirdSildeIn {
from {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
@keyframes thirdSildeIn {
from {
    -webkit-transform: translate3d(-33vw, 0, 0);
    transform: translate3d(-33vw, 0, 0);
}
to {
    -webkit-transform: none;
    transform: none;
}
}
.slideIn {
  -webkit-animation-name: slideIn;
  animation-name: slideIn;
}
.slideOut {
  -webkit-animation-name: slideOut;
  animation-name: slideOut;
}
.thirdSildeOut {
  -webkit-animation-name: thirdSildeOut;
  animation-name: thirdSildeOut;
}
.thirdSildeIn {
  -webkit-animation-name: AutoFileName;
  animation-name: thirdSildeIn;
}



